body {
    background-color: #acadbd;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.content {
    min-height: calc(100vh - 70px - 64px);
}

.progress {
    display: flex;
    justify-content: center;
    height: 100px;
    background-color: #626480;
}

.preloader-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 70px auto;
}

.list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
}

.card {
    display: flex;
    flex-direction: column;
}

.card-content {
    flex-grow: 1;
}

img,
iframe {
    max-width: 100%;
}

iframe {
    width: 600px;
    height: 350px;
}

.btn,
.btn-large,
.btn-small {
    background-color: #7a7ea5;
}

/*Style for footer*/
.footer__columns {
    display: flex;
    margin: 36px 0;
    align-items: center;
}

.footer__column {
    display: flex;
    flex-direction: column;
    margin-right: 120px;
    color: white;
}

.footer__column a {
    color: white;
}

/*Style for sec-2*/
.sec-2_compressed {
    display: flex;
    justify-content: space-between;
}

.sec-2__column2 {
    display: flex;
    flex-direction: column;
    width: 64%;
}

.sec-2__cards {
    display: flex;
    justify-content: space-between;
}

.sec-2__card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.sec-2__square {
    padding: 0px 20%;
    border: 2px solid #626480;
}

.vv {
    background-color: #626480;
}

.page-footer {
    background-color: #626480;
}